<template>
  <section class="app-main">
    <transition name="fade" mode="out-in">
       <keep-alive>
          <router-view :key="key" />
      </keep-alive>
    </transition>
    <!-- <div v-if="$store.state.settings.showFooter" style="height:20px" id="el-main-footer">
      <span v-html="$store.state.settings.footerTxt" style="font-size:10px;"/>
      <span> ⋅ </span>
      <a href="http://www.beian.miit.gov.cn" style="font-size:14px;" target="_blank">{{ $store.state.settings.caseNumber }}</a>
    </div> -->
  </section>
</template>

<script>
export default {
  name: 'AppMain',
  computed: {
    cachedViews() {
      return this.$store.state.tagsView.cachedViews
    },
    key() {
      return this.$route.path
    }
  }
}
</script>

<style lang="scss" scoped>
.app-main {
  /* 50= navbar  50  */
  min-height: calc(100vh - 50px);
  width: 100%;
  position: relative;
  overflow: hidden;
}

.fixed-header+.app-main {
  padding-top: 50px;
}

.hasTagsView {
  .app-main {
    /* 84 = navbar + tags-view = 50 + 34 */
    min-height: calc(100vh - 84px);
  }

  .fixed-header+.app-main {
    padding-top: 84px;
  }
}
</style>

<style lang="scss">
// fix css style bug in open el-dialog
.el-popup-parent--hidden {
  .fixed-header {
    padding-right: 15px;
  }
}
</style>


